//Tp tin lu cc d liu hng s hoc cc utility function

import axios from "axios";
import { customNavigate } from "..";

export const USER_LOGIN = 'userLogin';
export const TOKEN = 'accessToken';
export const DOMAIN = 'https://shop.cyberlearn.vn';

const configClient = {
    setStoreJson: (name, data) => {
        let sData = JSON.stringify(data);
        localStorage.setItem(name, sData);
    },
    getStoreJson: (name) => {
        if (localStorage.getItem(name)) {
            let sData = localStorage.getItem(name);
            let data = JSON.parse(sData);
            return data;
        }
        return {};
    },
    //lu chui
    setStore: (name, data) => {
        localStorage.setItem(name, data);
    },
    //ly chui
    getStore: (name) => {
        if (localStorage.getItem(name)) {
            return localStorage.getItem(name);;
        }
        return undefined;
    }, setCookieJson(name, value, days) {
        //Bin i data thnh string
        value = JSON.stringify(value);
        var expires = "";
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
            expires = "; expires=" + date.toUTCString();
        }
        document.cookie = name + "=" + (value || "") + expires + "; path=/";
    }, getCookieJson(name) {
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ') c = c.substring(1, c.length);
            if (c.indexOf(nameEQ) == 0) {
                return JSON.parse(c.substring(nameEQ.length, c.length))
            }
        }
        return null;
    }, deleteCookie(name) {
        document.cookie = name + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
    }
}


export const { setStoreJson, getStoreJson, setStore, getStore,getCookieJson,setCookieJson,deleteCookie } = configClient;

//To bn sao ca axios 
export const http = axios.create({
    baseURL: DOMAIN,
    
});

//Cu hnh interceptor cho request (api gi i)
http.interceptors.request.use((req)=> {
    // req.data = {...req.data,'abc':'123'}
    req.headers = {...req.headers,
        TokenCybersoft: `eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0ZW5Mb3AiOiJCb290Y2FtcCA0NSIsIkhldEhhblN0cmluZyI6IjIwLzExLzIwMjMiLCJIZXRIYW5UaW1lIjoiMTcwMDQzODQwMDAwMCIsIm5iZiI6MTY3MjA3NDAwMCwiZXhwIjoxNzAwNTg2MDAwfQ.nqyOmcwBXyqINN0ROA_xI8TKx0Jk05_lwRy4Cdv0j_8`
    }
    if(getStoreJson(USER_LOGIN)){
        req.headers = {...req.headers,
            Authorization: `Bearer ${getStoreJson(USER_LOGIN).accessToken}`
        }
    }


    return req;
},err => {
    return Promise.reject(err)
})

//Cu hnh cho interceptor cho response (kt qu tr v)
http.interceptors.response.use((res)=> {
    // res.data.result = 'abc';
    console.log(res.data)
    //Tt c kt qu tr v t http u chy hm ny
    return res;
}, err => {
    //X l li 
    if(err.response?.status === 401) {
        //Nu cha  quyn th  v login
        customNavigate.push('/login');
        return;
    }
    if(err.response?.status === 400 || err.response?.status  === 404){
        customNavigate.push('/');
        return;

    }
    return Promise.reject(err);
});
/* 
    request: l d liu gi ln server
    response: l d liu tr v t server
    status thng dng:
    200: OK D liu thao tc trn server thnh cng v tr v client
    201: D liu  c khi to  pha server
    400: bad request (d liu gi i khng hp l)
    404: not found (khng tm thy d liu - do sai link api )
    500: error in server (li xy ra ti server nguyn nhn c th cho frontend gi sai nh dng hoc backend x l logic b li)
    401: unauthorize (khng c quyn truy cp vo api - c th do thiu token hoc token ht hn)
    403: forbiden (cha  quyn truy cp vo api )
*/